* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.caruselBox {
  width: 500px;
  height: 350px;
  outline: 1px solid #000;
  flex-direction: row;
  justify-content: center;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
}

#prevImg, #nextImg {
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  display: inline-block;
}

#prevImg {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translate(0, -50%);
}

#nextImg {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(0, -50%);
}

.icon {
  stroke-width: 1px;
  stroke: #000;
  fill: #639;
  cursor: pointer;
  display: inline-block;
}

.elem {
  width: 100%;
  outline: 1px solid #bab2b2;
  display: none;
}

.active {
  width: 100%;
  display: block;
}

.img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.dotsBox {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}

.dotsList {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 3px;
  list-style: none;
  display: flex;
}

.dotImg {
  width: 10px;
  height: 10px;
  color: wheat;
  border: 1px solid #212121;
  border-radius: 50%;
}

.active-dot {
  background-color: #7cfc00;
}

